import React from 'react'
export default function Dashboard() {
  return (
    <section className='dashboard'>
      <div className='wrapper'>
        <h2>Flg med i <span className='blue-highlight'>dine</span> resultater</h2>
        <p>Med vores dashboard vil I kunne flge med i de resultater vi skaber. Dashboardet leveres skrddersyet til jer, i lige prcis det format I foretrkker.</p>
        <img src={require('../../images/dashboard.jpg')} alt=""></img>
      </div>
    </section>
  )
}